	module.exports = {"layout":"post","title":"Why I wake up.","published":true,"tags":["thoughts","creation"],"summary":null,"body":"<h2>To help people.</h2>\n<p>Quite simple.</p>\n<ul>\n<li>I help companies streamline their company through business technology.</li>\n<li>I help companies improve their products through a better user experience.</li>\n<li>I help humans by assisting companies in scaling their technology product\nto the point of creating new jobs.</li>\n<li>I help humans by educating my fellow web developers to do the same.</li>\n</ul>\n<p>What I do matters. What you do matters, too.</p>\n"}

