	module.exports = {"layout":"post","title":"Browser Stories.","published":true,"tags":["thoughts"],"summary":null,"body":"<p>There has always been something about Microsofts copy that\nbothers me. From their marketing copy on the IE download page, to their\nIE developer blog. I never quite could quite tell why.</p>\n<p>Was it the arrogant tone with pleaful text shouting, Look!\nWere faster than Chrome with these benchmarks we wrote!?</p>\n<p>Was it the constant digs at other browser vendors in the midst of\nan otherwise sensible post?</p>\n<p>No, no. Neither of these were the core of my discomfort.</p>\n<p>You see, I have been thinking about my purpose in doing what I do, what are\nthe qualities in the men and women I want to have close relationships\nwith. Then it struck me. The complete and utter difference of tone\namongst the browser vendors.</p>\n<p>Disclaimer: I am primarily a Chrome user. This came about because I\nstarted getting serious about web development shortly before Chrome came\nout and I its developer tools blew Firebug out of the water. And\nGoogle was telling a better story with Chrome.</p>\n<h2>A short survey on the purpose of browser vendors.</h2>\n<h3>Mozilla</h3>\n<p>To improve the web for humans through safety, personalization, and\nsecurity. To make the web ubiquitious. (Sure, their primary mission\nhas shifted slightly in recent years).</p>\n<p>Purpose: the betterment of human lives.</p>\n<h3>Google</h3>\n<p>To push the web platform forward. To improve the lives of web developers\neverywhere. To make the web ubiquitious.</p>\n<p>Purpose: the betterment of human lives.</p>\n<h3>Apple</h3>\n<p>In all things, be user-centered. Provide users with the best experience.</p>\n<p>Purpose: the betterment of human lives.</p>\n<h3>Opera</h3>\n<p>Push the web platform forward. Be a voice of change and reason in the\nvendor community and a staple in the standards process.</p>\n<p>Purpose: the betterment of human lives</p>\n<h3>Microsoft</h3>\n<p>Be number one in the market.</p>\n<p>Purpose: survive, thrive, and somehow hopefully even <em>win</em> against the\ncompetition.</p>\n<h2>The Contrast</h2>\n<p>This is not a direct endorsement or dig against any of these companies\nnow any individuals within them. Each of these companies are filled with\nnoble individuals with noble intentions.</p>\n<p>Stories are powerful. They shape our lives. The stories we tell are\nimpacting and changing the landscape around us.</p>\n<p>In the many months since I first wrote this draft Microsoft has\nbeen changing. The IE developer relations team behind <a href=\"http://modern.ie\">modern.ie</a> are\ndoing a far superior job changing the story that Microsoft is telling.</p>\n"}

