	module.exports = {"layout":"post","title":"CSSOM Smooth Scrolling","tags":["web development"],"summary":null,"body":"<h2>Coming soon to a browser near you</h2>\n<p>Paul Irish recently shared on <a href=\"https://plus.google.com/+PaulIrish/posts/XgLrpPS8vm8\">Google+</a>\nthat the CSSWG has <a href=\"http://dev.w3.org/csswg/cssom-view/#scroll-an-element-into-view\">standardized smooth\nscrolling</a> on behalf of web developers everywhere. This means no more jQuery or other such javascript to simply scroll to an element.</p>\n<p>In the future youll be able to declare smooth scrolling in your\ncss with <code>body { scroll-behavior: smooth }</code> or programmatically by using\n<code>window.scrollTo(x, y, 'smooth');</code> or\n<code>document.getElementById('offscreenElement').scrollIntoView(true, 'smooth')</code>.</p>\n<p>Yet another fine example of browsers standardizing a behavior developers\nhave been using for years.</p>\n<h2>Available today to a polyfill near you</h2>\n<p>I have wrapped the programmatic version into a simple polyfill. You can\ngrab it off my github account <a href=\"https://github.com/iamdustan/smoothscroll\">iamdustan/smoothscroll</a>\nor install it with <code>bower install smoothscroll --save</code>.</p>\n<p>Ive never attempted to polyfill a css property, but I hope to\nfind a solution so that simply including this script on the page and\nadding <code>body { scroll-behavior: smooth }</code> will give you smooth scrolling\nfor all internal anchors.</p>\n<p>Oh, and dont worry about file size. Its only 243 bytes\ngzipped.</p>\n"}

