	module.exports = {"layout":"post","title":"Books of 2013","tags":["reading list"],"summary":null,"body":"<p>After accidentally reading 42 books in 2012, I had quietly set myself\nthe goal of reaching 52 books in 2013. With a last minute book off of\nLean Pub I pulled it off with 1 minute 12 seconds to spare.</p>\n<h2>The List</h2>\n<table class=\"numbered\">\n  <thead>\n    <tr>\n      <th scope=\"col\"><span class=\"visuallyhidden\">Number</span></th>\n      <th scope=\"col\">Book</th>\n      <th scope=\"col\">Author</th>\n      <th scope=\"col\" style=\"width:7em\">Finished Date</th>\n    </tr>\n  </thead>\n  <tbody>\n    {% for book in site.data.2013_books %}\n    <tr>\n      <td>{{ forloop.index }}</td>\n      <td>{{ book.title }}</td>\n      <td>{{ book.author }}</td>\n      <td>{{ book.completed_date }}</td>\n    </tr>\n    {% endfor %}\n  </tbody>\n</table>\n<h2>Highlights</h2>\n<p>There are a few stand out books from this list. First and foremost,\n<em>Thinking Fast and Slow</em> by Daniel Kahneman is a great book. I am\nfascinated by how the human mind works and this was my grand\nintroduction to psychology. He discusses how our mind works as two\ndifferent systems in an engaging way that allows for understanding to\ncome as you read through. I cannot recommend this book enough.</p>\n<p>Having read the <em>Enders Game</em> quartet last year, I picked up the <em>Bean\nQuartet</em> by Orson Scott Card this year. The first book is the exact same\nstory as <em>Enders Game</em>, but through Beans eyes. The\nfollowing three books are a much faster paced adventure through an earth\nin a not-to-distant future.</p>\n<p><em>Love Wins</em> by Rob Bell is a book that received much attention when it\ncame outand not positive attention I must say that as a\nChristian it greatly challenged my view of God and His love. If nothing\nelse, it shows Gods love in such a greater light than what I had\never imagined.</p>\n<p>I cant remember a single technical book Ive read before\nthis year that I would strongly recommend. These year there were four.</p>\n<p>David Herman and Reg Braithwaite have written the two best books on\nJavaScript. Effective JavaScript and JavaScript Allong. If you spend\nany time at all in JavaScript at a professional level you should read\nthese books.</p>\n<p>Effective JavaScript is a very thorough, dispassionate\nexplanation of the JavaScript language. It does not dwell on browser\nidiosyncracies, yet equips you to understand and handle them should you\nencounter them.</p>\n<p>JavaScript Allong is a work of art. Read the reviews on\n<a href=\"https://leanpub.com/javascript-allonge\">https://leanpub.com/javascript-allonge</a>. Every single one of them: true.</p>\n<p>Ilya Grigorik delves into HTTP, TCP, UDP, WebRTC, WebSockets, <strong>and more</strong>\nin his <em>High-Performance Browser Networking</em>. Being a front end engineer\nfor the past 3 years you may expect one to have a fairly solid grasp on\nthese protocols and their history. I knew nothing about networking before\nthis book.</p>\n<p><em>The Senior Software Engineer</em> by David Bryant Copeland took many of my\nthoughts about this craft and said them eloquently. It helped solidify a\nfew ideas and expand others. Every software engineer should read this,\njunior or not.</p>\n<h2>2014?</h2>\n<p>No. No book reading goal. I am still reading, just for pleasure at\nwhatever pace it happens to be. Currently, Im embarking on a\nadventure through the seven kingdoms in <em>Game of Thrones</em>.</p>\n<p>I have other goals to focus on now that I have some time back. I have a\nhandful of public goals on <a href=\"https://github.com/iamdustan/goals/issues\">github</a></p>\n"}

