	module.exports = {"layout":"post","title":"Flexbox your buttons.","tags":["css3 flexbox"],"summary":null,"body":"<p>There are certain properties that are not consistently applied across\nelements. If youve ever tried to style a <code>&lt;select /&gt;</code> or <code>&lt;input type=&quot;file&quot; /&gt;</code> element you have experienced this.</p>\n<p>This doesnt apply only to the visual styles we were pained by in the past, but\nalso by layout properties such as <code>display: block;</code>.</p>\n<p>I am working on a Phonegap project these days so I have a rather narrow range of\nrendering engines to support. The layout for most of the app just totally ate it\non Android 4.3. I chocked it up to just being part of <a href=\"http://slides.com/html5test/the-android-browser#/\">the android\nbrowser</a>.</p>\n<p>Then I switched to using Firefox as my primary browser and the same layout bug\nwas staring me in the face.</p>\n<p>A simplified version looks like the following:</p>\n<pre><code class=\"language-html\"><span class=\"hljs-tag\">&lt;<span class=\"hljs-name\">style</span>&gt;</span><span class=\"css\">\n<span class=\"hljs-selector-tag\">button</span> {\n  <span class=\"hljs-attribute\">display</span>: flex;\n  <span class=\"hljs-attribute\">width</span>: <span class=\"hljs-number\">100%</span>;\n}\n\n<span class=\"hljs-selector-tag\">button</span> <span class=\"hljs-selector-tag\">div</span> {\n  <span class=\"hljs-attribute\">flex</span>: <span class=\"hljs-number\">1</span>;\n  <span class=\"hljs-attribute\">color</span>: <span class=\"hljs-number\">#fff</span>;\n  <span class=\"hljs-attribute\">background</span>: <span class=\"hljs-number\">#ff8800</span>;\n}\n<span class=\"hljs-selector-tag\">button</span> <span class=\"hljs-selector-tag\">div</span><span class=\"hljs-selector-pseudo\">:first-child</span> {\n  <span class=\"hljs-attribute\">background</span>: <span class=\"hljs-number\">#ff0088</span>;\n}\n</span><span class=\"hljs-tag\">&lt;/<span class=\"hljs-name\">style</span>&gt;</span>\n\n<span class=\"hljs-tag\">&lt;<span class=\"hljs-name\">button</span>&gt;</span>\n  <span class=\"hljs-tag\">&lt;<span class=\"hljs-name\">div</span>&gt;</span>Left<span class=\"hljs-tag\">&lt;/<span class=\"hljs-name\">div</span>&gt;</span>\n  <span class=\"hljs-tag\">&lt;<span class=\"hljs-name\">div</span>&gt;</span>Right<span class=\"hljs-tag\">&lt;/<span class=\"hljs-name\">div</span>&gt;</span>\n<span class=\"hljs-tag\">&lt;/<span class=\"hljs-name\">button</span>&gt;</span>\n</code></pre>\n<p><a href=\"http://jsbin.com/jutafira/2/embed\">JS Bin</a></p>\n<figure>\n  <img src=\"flex-button-in-gecko.png\">\n  <figcaption>Flex button in Gecko</figcaption>\n</figure>\n<figure>\n  <img src=\"flex-button-in-blink.png\">\n  <figcaption>Flex button in Blink</figcaption>\n</figure>\n<p>Now I realized it wasnt just the Android view that was buggy, but perhaps\nFireFox as well.</p>\n<p>It turns out that this <em>isnt</em> a bug. There is ambiguity in the spec that certain\nelements have certain properties that are implementation-specific. It just so\nhappened that certain versions of Blink and Webkit allow <code>display: flex</code> on\n<code>button</code>s where as Gecko and other versions of Webkit do not. I am unsure what\nTrident thinks of this.</p>\n<p>(See <a href=\"https://twitter.com/CodingExon\">@dholbert</a> response to this issue on\n<a href=\"https://bugzilla.mozilla.org/show_bug.cgi?id=984869#c2\">Bugzilla #984869</a>)</p>\n<h1>TLDR</h1>\n<p>All of that to say that the following does not work:</p>\n<pre><code class=\"language-html\"><span class=\"hljs-tag\">&lt;<span class=\"hljs-name\">div</span> <span class=\"hljs-attr\">class</span>=<span class=\"hljs-string\">\"list-of-actionable-things\"</span>&gt;</span>\n  <span class=\"hljs-tag\">&lt;<span class=\"hljs-name\">button</span> <span class=\"hljs-attr\">class</span>=<span class=\"hljs-string\">\"flex\"</span>&gt;</span>\n    <span class=\"hljs-tag\">&lt;<span class=\"hljs-name\">div</span> <span class=\"hljs-attr\">class</span>=<span class=\"hljs-string\">\"flex-1\"</span>&gt;</span><span class=\"hljs-tag\">&lt;<span class=\"hljs-name\">img</span> <span class=\"hljs-attr\">src</span>=<span class=\"hljs-string\">\"/an/avatar/\"</span> /&gt;</span><span class=\"hljs-tag\">&lt;/<span class=\"hljs-name\">div</span>&gt;</span>\n    <span class=\"hljs-tag\">&lt;<span class=\"hljs-name\">div</span> <span class=\"hljs-attr\">class</span>=<span class=\"hljs-string\">\"flex-2\"</span>&gt;</span><span class=\"hljs-tag\">&lt;<span class=\"hljs-name\">strong</span>&gt;</span>Username<span class=\"hljs-tag\">&lt;/<span class=\"hljs-name\">strong</span>&gt;</span><span class=\"hljs-tag\">&lt;/<span class=\"hljs-name\">div</span>&gt;</span>\n    <span class=\"hljs-tag\">&lt;<span class=\"hljs-name\">div</span> <span class=\"hljs-attr\">class</span>=<span class=\"hljs-string\">\"flex-2\"</span>&gt;</span><span class=\"hljs-tag\">&lt;<span class=\"hljs-name\">span</span>&gt;</span>Last updated: yesterday<span class=\"hljs-tag\">&lt;/<span class=\"hljs-name\">span</span>&gt;</span><span class=\"hljs-tag\">&lt;/<span class=\"hljs-name\">div</span>&gt;</span>\n  <span class=\"hljs-tag\">&lt;/<span class=\"hljs-name\">button</span>&gt;</span>\n  <span class=\"hljs-comment\">&lt;!-- ... --&gt;</span>\n<span class=\"hljs-tag\">&lt;/<span class=\"hljs-name\">div</span>&gt;</span>\n</code></pre>\n<p>To get around this you need to put an extra element inside the <code>button</code>.</p>\n<pre><code class=\"language-html\"><span class=\"hljs-tag\">&lt;<span class=\"hljs-name\">div</span> <span class=\"hljs-attr\">class</span>=<span class=\"hljs-string\">\"list-of-actionable-things\"</span>&gt;</span>\n  <span class=\"hljs-tag\">&lt;<span class=\"hljs-name\">button</span>&gt;</span>\n    <span class=\"hljs-tag\">&lt;<span class=\"hljs-name\">div</span> <span class=\"hljs-attr\">class</span>=<span class=\"hljs-string\">\"flex\"</span>&gt;</span>\n      <span class=\"hljs-tag\">&lt;<span class=\"hljs-name\">div</span> <span class=\"hljs-attr\">class</span>=<span class=\"hljs-string\">\"flex-1\"</span>&gt;</span><span class=\"hljs-tag\">&lt;<span class=\"hljs-name\">img</span> <span class=\"hljs-attr\">src</span>=<span class=\"hljs-string\">\"/an/avatar/\"</span> /&gt;</span><span class=\"hljs-tag\">&lt;/<span class=\"hljs-name\">div</span>&gt;</span>\n      <span class=\"hljs-tag\">&lt;<span class=\"hljs-name\">div</span> <span class=\"hljs-attr\">class</span>=<span class=\"hljs-string\">\"flex-2\"</span>&gt;</span><span class=\"hljs-tag\">&lt;<span class=\"hljs-name\">strong</span>&gt;</span>Some text<span class=\"hljs-tag\">&lt;/<span class=\"hljs-name\">strong</span>&gt;</span><span class=\"hljs-tag\">&lt;/<span class=\"hljs-name\">div</span>&gt;</span>\n      <span class=\"hljs-tag\">&lt;<span class=\"hljs-name\">div</span> <span class=\"hljs-attr\">class</span>=<span class=\"hljs-string\">\"flex-2\"</span>&gt;</span><span class=\"hljs-tag\">&lt;<span class=\"hljs-name\">span</span>&gt;</span>Last updated: yesterday<span class=\"hljs-tag\">&lt;/<span class=\"hljs-name\">span</span>&gt;</span><span class=\"hljs-tag\">&lt;/<span class=\"hljs-name\">div</span>&gt;</span>\n    <span class=\"hljs-tag\">&lt;/<span class=\"hljs-name\">div</span>&gt;</span>\n  <span class=\"hljs-tag\">&lt;/<span class=\"hljs-name\">button</span>&gt;</span>\n  <span class=\"hljs-comment\">&lt;!-- ... --&gt;</span>\n<span class=\"hljs-tag\">&lt;/<span class=\"hljs-name\">div</span>&gt;</span>\n</code></pre>\n<p><a href=\"http://jsbin.com/jutafira/3/embed\">JS Bin</a></p>\n<figure>\n  <img src=\"flex-button-in-gecko.png\">\n  <figcaption>Flex buttons everywhere</figcaption>\n</figure>\n"}

