	module.exports = {"layout":"post","title":"Learning ClojureScript. For JavaScript Developers","tags":["clojurescript"],"summary":null,"body":"<p>Ive wanted to add a functional language to my knowledge belt for quite some\ntime now. If you check out my <a href=\"/2014/01/09/reading-list/\">2013 reading list</a> you\nmay have noticed a Haskell book in there. Haskell was my introduction to thinking\nwithout state, but rather with immutable data structures and the syntax was so\nentirely different from what I was used to with JavaScript that I could not\nmerely port my knowledge for how to solve a problem over in most situations.</p>\n<p>Haskell didnt have much practical benefit to me, either. After getting through\nthe book there was nothing pushing me onward.</p>\n<p>Ive recently begun my second attempt into the functional world, this time with\nClojureScript. I intend on writing about this journey over the next few months,\nso lets get started.</p>\n<h2>Why a functional language?</h2>\n<p>It took only a tiny introduction to the functional world to realize how\ndifferent solving problems could be. My slight foray into Haskell already\nchanged how I think about building applications and organizing code. I figure I\ncould get more bang for the educational buck by learning a fundamentally\ndifferent way of solving problems with a functional language compared to picking\nup another OOP language.</p>\n<h2>Why ClojureScript?</h2>\n<p>There are two closely related, yet distinct points to this that I think are\nworth mentioning.</p>\n<ol>\n<li>Why Clojure/ClojureScript over F#, Scheme, Haskell, OCaml, Erlang,\n<a href=\"http://en.wikipedia.org/wiki/Category:Functional_languages\">et cetera</a>?</li>\n<li>Why do I think Ill stick through to competency with the language?</li>\n</ol>\n<h3>Why Clojure?</h3>\n<p>There are some circumstantial reasons that have led to Clojure being on my\nradar.</p>\n<p>Ive used many of the leading JavaScript frameworks in production, most recently\nReact. React is the first tool Ive used that didnt leave a bad taste in my\nmouth when I was finished. In fact, Ive rather enjoyed every moment of it and\ncouldnt imagine building a browser-based application without it.</p>\n<p>One of the books that I started reading this year was the <a href=\"http://mitpress.mit.edu/sicp/full-text/book/book.html\">Structure and\nInterpretation of Computer\nPrograms</a>. I have only\nread the first three sections of the first chapter, but that gave me enough lisp\nknowledge to understand the basic semantics of languages that follow the lisp\nstructure.</p>\n<p>During this time, <a href=\"http://swannodette.github.io/\">David Nolen</a> started appearing\nagain and again in my feed and was talking about all this ClojureScript,\nimmutable data, core.async stuff. (Aside: it may be worth adding that I\nfundamentally believe that the default complex JavaScript codebase decoupling\nthrough arbitrary events pattern to be a poor choice, and that my time in the\nAngular world was on a team that found the best way to pass data around to just\narbitrarily mutate the nearest object.)</p>\n<p>Since my experience and relevancy is rooted entirely in building user interfaces\nin the browser world, tools and approaches that I can relate to my problem set\nwin over other concepts, such as the proof-iness that Haskell provides.</p>\n<h3>Why will I stick with it?</h3>\n<p>This is the sticking point. I am a very driven, self-motivated individual, but\nam terrible at focusing on any one thing for long enough to complete it. I am\nregularly spinning up new experiments and rewriting side projects long before\ngetting them out the door.</p>\n<p>Because ClojureScript can help solve problems I have building user interfaces is\na huge plus to sticking this through. Honestly, thats probably the primary\nreason that I will continue with it for any length of time.</p>\n<p>Everything else will be myself experimenting on myself. Setting myself a\ncommitment to write about my journey I hope is enough perceived social pressure\nto keep going.</p>\n<h2>What to expect next?</h2>\n<p>My current expectation is that the next two posts in the <a href=\"/tags/clojurescript/\">Learning ClojureScript\nfor JavaScripters</a> series will be a just enough syntactic\noverview, and then how to solve some common JavaScript problems in ClojureScript.</p>\n"}

