	module.exports = {"layout":"post","title":"Books of 2014","tags":["reading list"],"summary":null,"body":"<p>2014 was my year off from reading. Well, I read the Game of Thrones series. Now\nI sit here waiting for George R.R. Martin to finish the Winds of Winter so I can\nmove on with my life for a month before waiting another 7 years for the 7th\ninstallation in the series. But alas, here is the list:</p>\n<h2>The List</h2>\n<table class=\"numbered\">\n  <thead>\n    <tr>\n      <th scope=\"col\"><span class=\"visuallyhidden\">Number</span></th>\n      <th scope=\"col\">Book</th>\n      <th scope=\"col\">Author</th>\n      <th scope=\"col\" style=\"width:7em\">Finished Date</th>\n    </tr>\n  </thead>\n  <tbody>\n    {% for book in site.data.2014_books %}\n    <tr>\n      <td>{{ forloop.index }}</td>\n      <td>{{ book.title }}</td>\n      <td>{{ book.author }}</td>\n      <td>{{ book.completed_date }}</td>\n    </tr>\n    {% endfor %}\n  </tbody>\n</table>\n"}

