	module.exports = {"layout":"post","title":"How to choose a second language","tags":["learning"],"summary":null,"body":"<p>So youve been programming for a while now and you want to explore what other\nlanguages are there. How do you choose?</p>\n<p>Do you choose based on how you think you might get a job?\nOr do you choose where all the excitement is?</p>\n<p>My advice? Find something that will <em>change the way you think about\nprogramming</em>.</p>\n<p>Most likely youve been writing in some form of object-oriented language. Thats\ngreat. Its what the vast majority of professional software developers author\nin.</p>\n<p><em>Please</em>, dont choose another OO language. Choose a language that has\nfundamentally different ideas about programming. Or choose a language that aims\nto solve an entirely different problem space. Choose a <strong>functional language</strong>.\nChoose a <strong>systems language</strong>. Choose an obscure or esoteric language. Its\nimpact on you will be far greater.</p>\n<p>For example, Ive been a fronteer my entire professional career. This means my\nprimary programming language has been JavaScript. Despite its warts, I love\nJavaScript. Likely for the same reason I love the English language. Its the way\nmy brain models the world. Thus, I could choose a related language that I could\neasily apply at work such as C# or Ruby. This will feel familiar in how I model\nthe world, and its relatively simple to adjust to. Learning the syntax and the\nidioms of the community and Im off to the races.</p>\n<p>The trouble for me is I cant understand how <em>I</em> become better. Ruby and C# are\ntoo close to my mother tongue.</p>\n<p>I choose to learn another language because I want to fundamentally change the\nway I think about programming.</p>\n<p>Functional programming is fundamentally different than object oriented. Systems\nprogramming is fundamentally different than UI and web programming.</p>\n<p>Im going to learn a functional language. I might even learn a systems language.\nYou should join me.</p>\n"}

