	module.exports = {"layout":"post","title":"The End of a Chapter","tags":["work"],"summary":null,"body":"<p>This week marks the end of the best chapter of my career. The past four years at\n<a href=\"http://www.skookum.com\">Skookum</a> have without question changed my life. I came\nin knowing hardly a thing and today am a mostly competent developer. At Skookum\nI have worked alongside some of the smartest people I have ever met.</p>\n<p>Im very proud of what weve been able to accomplish over the past few years.\nWeve shipped such a vast array of projects with such a diverse set of\ntechnologies. We have had a direct impact on many companies success in the\nmarket and improved the lives of our clients employees through the time savings\nweve enabled them to make.</p>\n<p>About a year ago I changed my role to work less on our client projects and focus\non our development culture and investing in our community. Skookum now hosts\nmany tech meetup events including the Skookum Tech Talks, CharlotteJS, iOS,\nAndroid, Google Developer, and Functional Programming (okay, fine, that one is\nbasically non-existent still) meetups every month. Weve invested in the\ncommunity through sponsoring Blend Conf or our annual multi-meetup holiday\nparty, and attendance to hackathons such as CharlotteHeartsGigabit.</p>\n<p>On the technical side, weve been working to objectively increase the caliber of\nthe products we release by having better access to continuous integration tools,\nemploying static analysis tools, linters, and other metrics to ensure were\ndelivering stable products.</p>\n<p>Skookum isin my totally unbiased opinionthe best team to work for in\nCharlotte, NC. Even though my time as a paid team member is ending I will\ncontinue to be involved through continuing to organize the Skookum Tech Talks\nand CharlotteJS.</p>\n<p>Thank you, Skookum, for the opportunity to be known as on of you for a season.</p>\n"}

