	module.exports = {"layout":"post","title":"React Custom Renderers","tags":["react"],"summary":null,"body":"<p>Youre considering building a custom renderer for React and are wondering where to start and what to do? Welcome (to the land of dragons)! Just kidding, its not that badjust undocumented and not officially supported. \\_()_/</p>\n<p>This article is intended to be a high level view of the developer experience a renderer should aim to provide based on my experience with React, writing a renderer, and exploring other custom renderers. Note that this is <em>not</em> a guide on how to write a renderer.</p>\n<p>tldr;</p>\n<ul>\n<li>React devs are Web devs. Design your APIs accordingly.</li>\n<li>Hide imperative APIs behind declarative ones. This is what makes React, React.</li>\n<li>Progressive disclosure. Use invariants and warnings to educate consumers <em>while they use it</em>.\nInterop with existing tools such as live reload, React DevTools, and module bundlers.</li>\n<li>Bonus: Share your creations and learnings.</li>\n</ul>\n<h2>React Developers are Web Developers</h2>\n<p>The majority of React Developers are Web Developers first. At the very least the React community is rooted in the web platform. A renderer should implement web-centric APIs on top of the target platform where it makes sense.</p>\n<p>For example, you should strongly consider providing CSS Layout for positioning and styling.</p>\n<p>If there are form-style input controls they should follow DOM and React controlled vs uncontrolled semantics. Standard form controls such as tab focus, return key, and other expected navigation controls should just work.</p>\n<p>Learn once, write anywhere should be your guiding principle when considering the public API of your renderer.</p>\n<h2>Hide imperative APIs behind declarative ones</h2>\n<p>The public API of components is far more important than the specifics of their implementation. It is the Renderers responsibility to take the weight of the platform and simplify it. React does this repeatedly. It takes a stateful world like the DOM and presents you an interface that allows you to simply describe what the DOM should be.</p>\n<p>The platform APIs should be accessible as an escape hatch when required. As an example, in the browser environment we escape out of the declarative React authoring model to imperatively manage focus.</p>\n<p>As a guideline, a sure sign that there is a better abstraction for you to provide is that your renderer requires the consumer to write lifecycle methods to interact with the target environment to accomplish simple, declarative tasks.</p>\n<h2>Progressive Disclosure and Education</h2>\n<p>Help the developer along. Use warnings and invariants to provide educational messages as to why they are receiving that message and what can be done to rectify it.</p>\n<p>React does this when attempting to render a <tr /> directly into a <table />. React Hardware is taking the pin today from the board and verifying that an acceptable mode is being used.</p>\n<p>On React Native you are likely familiar with the Red and Yellowboxes (in your face variations on the former examples) which are incredibly helpful to use the project without having to query documentation endlessly.</p>\n<p>Development integration always trumps walls of documentation. Provide the knowledge needed when it is needed.</p>\n<h2>Interop with existing tools</h2>\n<p>The React ecosystem has flourished a bevy of tools. A handful of this could be considered core or foundational. It should be your aim to interop with these.</p>\n<p>Hot reloading has been transformative to many UIEs on the web and now with React Native, too. The React team supports Flow type definitions for static analysis regarding program correctness, safety, and documentation. The official React DevTools are a nice addition (not to mention the benefits of running and debugging code in a browser context). Lastly common module bundlers such as Webpack, Browserify, and Rollup should also be able to consume and build for your platform (if a simple <code>node index.js</code> does not suffice).</p>\n<h2>Share</h2>\n<p>As with anything in our software community, sharing is an enabler. Reacts public API is quite stable and predictable, but the internals are not quite so clear. The use cases and patterns you need to implement your renderer will be extremely useful to the React team as they move towards having a better story for custom renderers. Additional parts of the API are also still being thought up and iterated upon such as a DevTools API.</p>\n<p>Be a giant. Share what you learn.</p>\n<hr>\n<p>Thank you to <a href=\"https://twitter.com/zpao\">Paul OShannessy</a> for reviewing this article.</p>\n"}

