	module.exports = {"layout":"post","title":"An Experiment with React, My Voice, and Hardware.","tags":["react","react-hardware"],"summary":null,"body":"<p>Ive been playing with creating a React renderer for hardware off and on over\nthe past year. If you follow me on Twitter you may have seen that a milestone\nwas reached two days ago when <a href=\"https://twitter.com/iamdustan/status/698616826209501184\">React Hardware and the React Devtools became friends</a>.</p>\n<p>Ive been looking for a reason to play with the <a href=\"https://developer.mozilla.org/en-US/docs/Web/API/SpeechRecognition\">SpeechRecognition</a>\nAPI. Chrome implemented the API a year or so ago and Firefox has support coming\nquickly. With all the pieces in place it sounded like a fun way to spend an hour\nof my evening.</p>\n<p>The code in the following example will be cleaned up a tiny bit and pushed to\nGitHub this week, but I hope this video is as exciting for you as it is for me.</p>\n<p>React in a browser doing speech recognition to communicate with React hardware\nturning an LED on and off. All while conveniently viewing the render tree and\nstate from the comfort of the React Devtools.</p>\n<iframe width=\"420\" height=\"315\" src=\"https://www.youtube.com/embed/_Kz8lito3U8\" frameborder=\"0\" allowfullscreen></iframe>\n"}

