	module.exports = {"layout":"post","title":"daggumnpm: a friendly bash utility","tags":["bash"],"summary":null,"body":"<p>No matter what version of <a href=\"http://npmjs.com/\">npm</a> you use, you likely have had\nto destroy your <code>node_modules</code> folder and do a clean <code>npm install</code> to debug some\nissue. This likely isnt exclusive to npm either, but any package manager.\nRegardless, heres a quick friendly bash script to reinstall your <code>node_modules</code>\nfresh, let you know when its done, and automatically run the previous command.</p>\n<pre><code class=\"language-bash\"><span class=\"hljs-built_in\">alias</span> daggumnpm=<span class=\"hljs-string\">'rm -rf node_modules &amp;&amp; npm i &amp;&amp; say \"Rerunning `!!` &amp;&amp; !!'</span>\n</code></pre>\n<p>If youve used node at all the first two commands should be straightforward.</p>\n<ul>\n<li><code>rm</code>: remove things. <code>-rf</code> means recursively destroy a directory.</li>\n<li><code>npm i</code>: alias for <code>npm install</code></li>\n<li><code>say &quot;Rerunning \\</code>!!`&quot;: use your computers robot voice to audibly say Running\nwhatever that double exclamation point is.</li>\n<li><code>!!</code>: This is the fun one. Execute the previous command. <a href=\"https://www.digitalocean.com/community/tutorials/how-to-use-bash-history-commands-and-expansions-on-a-linux-vps#executing-commands-from-your-bash-history\">https://www.digitalocean.com/community/tutorials/how-to-use-bash-history-commands-and-expansions-on-a-linux-vps#executing-commands-from-your-bash-history</a>\nFor example, you ran <code>npm start</code> and it failed with some dependency issue.\n<code>daggumnpm</code> will do a clean npm install and rerun <code>npm start</code> for you.</li>\n</ul>\n"}

